import React from 'react'
import { Link } from 'react-router-dom'
import { connect } from 'react-redux';
import { createStructuredSelector } from 'reselect';
import {
  googleSignInStart,
  googleSignInStartNoSnapshot,
  emailSignInStart
} from '../../redux/user/user.actions';
import { selectCurrentUser } from '../../redux/user/user.selectors';
import {
  CButton,
  CCard,
  CCardBody,
  CCardGroup,
  CCol,
  CContainer,
  CForm,
  CInput,
  CInputGroup,
  CInputGroupPrepend,
  CInputGroupText,
  CRow
} from '@coreui/react'
import CIcon from '@coreui/icons-react'


class Login extends React.Component
{
  constructor(props) {
    super(props); 

    this.state = {
      user: {},
      email: '',
      password: ''
    };
  }

  componentDidMount() {
    /* if (user.providerData.length > 0) {
      let location = useLocation();
      let { from } = location.state || { from: { pathname: "/goaml" } };
      let history = useHistory();
      history.replace(from);
      history.push('/goaml');
    } */
  }

  handleSubmit = event =>
  {
    event.preventDefault();
    this.setState({email: "", password: "", user: { }})
  }

  handleLogin = () =>{
    const { googleSignInStart, currentUser } = this.props;
    googleSignInStart();
    this.setState({email: "", password: "", user: currentUser})
    console.log("Userrrrr: ", this.state.user);
  }

  handleSubmit = event =>
  {
    event.preventDefault();
    this.setState({email: "", password: "", user: { }})
  }

  handleChange = event =>
  {
    const {value, name} = event.target;
    this.setState({[name] : value})
  }

  render(){
    return (
      <div className="c-app c-default-layout flex-row align-items-center">
        <CContainer>
          <CRow className="justify-content-center">
            <CCol md="8">
              <CCardGroup>
                <CCard className="p-4">
                  <CCardBody>
                    <CForm>
                      <h1>Login</h1>
                      <p className="text-muted">Sign In to your account</p>
                      <CInputGroup className="mb-3">
                        <CInputGroupPrepend>
                          <CInputGroupText>
                            <CIcon name="cil-user" />
                          </CInputGroupText>
                        </CInputGroupPrepend>
                        <CInput type="text" placeholder="email" autoComplete="email" />
                        {/* <CInput type="text" name="email" value={this.state.email} onChange={this.handleChange} placeholder="email" autoComplete="email" /> */}
                      </CInputGroup>
                      <CInputGroup className="mb-4">
                        <CInputGroupPrepend>
                          <CInputGroupText>
                            <CIcon name="cil-lock-locked" />
                          </CInputGroupText>
                        </CInputGroupPrepend>
                        <CInput type="password" placeholder="Password" autoComplete="current-password" />
                        {/* <CInput type="password" name="password" value={this.state.password} placeholder="Password" autoComplete="current-password" /> */}
                      </CInputGroup>
                      <CRow>
                        <CCol xs="6">
                          <CButton color="primary" className="px-4">Login</CButton>
                        </CCol>
                        <CCol xs="6">
                          <input type="submit" onClick={this.handleLogin} value="Sign in with Google" className="btn btn-danger text-right"></input>
                        </CCol>
                      </CRow>
                    </CForm>
                  </CCardBody>
                </CCard>
                <CCard className="text-white bg-primary py-5 d-md-down-none" style={{ width: '44%' }}>
                  <CCardBody className="text-center">
                    <div>
                      <h2>Sign up</h2>
                      {/* <p>Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod tempor incididunt ut
                        labore et dolore magna aliqua.</p> */}
                      <Link to="/register">
                        <CButton color="primary" className="mt-3" active tabIndex={-1}>Register Now!</CButton>
                      </Link>
                    </div>
                  </CCardBody>
                </CCard>
              </CCardGroup>
            </CCol>
          </CRow>
        </CContainer>
      </div>
    )
  }
}


const mapDispatchToProps = dispatch => ({
  googleSignInStart: () => dispatch(googleSignInStartNoSnapshot()),
  emailSignInStart: (email, password) =>
    dispatch(emailSignInStart({ email, password }))
});

const mapStateToProps = state => ({
  currentUser: selectCurrentUser(state)
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Login);

